import React, {Component} from 'react';
import PropTypes from 'prop-types';

class Debuggers extends Component {
  render() {
    let {debuggers} = this.props;
    if (!debuggers) return null;

    let debugItems = debuggers.map((debuggerEntry, idx) => (
      <li key={Math.random()}>
        {debuggerEntry}
      </li>
    ));

    return (
      <div>
        {debugItems}
      </div>
    );
  }
}
Debuggers.propTypes = {
  debuggers: PropTypes.arrayOf(PropTypes.element)
};

export default Debuggers;



