import initControls from "./initControls";
import {buildBoxMatrix, animateBoxes} from './figures/boxMatrixBuilder';
import buildLegoBohrokSpinner from './figures/legoBohrokSpinner';
import buildRelic from './figures/relicBuilder';
import {buildBoxRelic, animateRelicBoxes} from './figures/boxRelic';
import buildMathSideTest from './figures/mathSideTest';
import buildReverseMesh from './figures/reverseMesh';
import './index.css';
import initSlidersDebugger from './utils/sliderDebuggerRenderer';

// three js was imported in index.html
// eslint-disable-next-line no-undef
let three = THREE;
const noop = () => {};


const FIGURE_INDEX_KEY = 'FIGURE_INDEX';


var renderer = null;
var scene = null;
var camera = null;
var controls = null;
main();


function createLegoBohrokSpinner() {
  init({
    autoRotate: true,
    autoRotateSpeed: 13,
    cameraPosition: [-1, 0.8, 5],
    buildPlacementFn: (scene, meshContext) => {
      buildLegoBohrokSpinner({
        scene,
        additionalPartsEnabled: true
      })
    }
  });
}

function createBoxes() {
  init({
    autoRotate: true,
    autoRotateSpeed: 5,
    buildPlacementFn: (scene, meshContext) => {
      buildBoxMatrix({
        matrixSize: 5,
        distance: 0.12,
        spinEveryBox: false,
        targetScene: scene,
        meshContext
      });
    },
    runSpecificAnimations: (meshContext) => animateBoxes(meshContext)
  });
}

function createRelic() {
  init({
    autoRotate: true,
    autoRotateSpeed: 20,
    buildPlacementFn: buildRelic
  });
}

function createMathSideTest() {
  init({
    autoRotate: true,
    autoRotateSpeed: 20,
    cameraPosition: [-1, 0.8, 5],
    buildPlacementFn: buildMathSideTest
  });
}

function createReverseMesh() {
  init({
    autoRotate: true,
    autoRotateSpeed: 20,
    buildPlacementFn: buildReverseMesh
  });
}

function createBoxRelic() {

  /*
   * if u set 'movingDistance': 0.015, u will have nuclear decay
   * PS: try with 'unlockImmediately': true
  */

  init({
    autoRotate: false,
    buildPlacementFn: (scene, meshContext) => buildBoxRelic({
      matrixSize: 5,
      distance: 0.2,
      movingDistance: 0.01,
      emptyBoxChance: 0.09,
      stayBoxChance: 0.98,
      unlockImmediately: false,
      spinningDistanceStep: 0.06,
      addSpinningEffectChance: 0.001,
      targetScene: scene,
      meshContext
    }),
    runSpecificAnimations: animateRelicBoxes
  });
}

function main() {
  let variants = [
    createBoxRelic,
    createLegoBohrokSpinner,
    createBoxes,
    createRelic,
    createMathSideTest,
    createReverseMesh
  ];

  let figureIndex = Number(localStorage.getItem(FIGURE_INDEX_KEY)) || 0;
  variants[figureIndex]();
  initSlidersDebugger();

  initControls({
    // Case matters!
    KeyA: () => {
      if (figureIndex === 0) return;

      figureIndex--;
      localStorage.setItem(FIGURE_INDEX_KEY, figureIndex);
      location.reload(); // eslint-disable-line no-restricted-globals
    },
    KeyD: () => {
      if (figureIndex === variants.length - 1) return;

      figureIndex++;
      localStorage.setItem(FIGURE_INDEX_KEY, figureIndex);
      location.reload(); // eslint-disable-line no-restricted-globals
    }
    });
}

function init({
  autoRotate = false,
  autoRotateSpeed = 88,
  cameraPosition=[-1, 0.6, 1.5],
  buildPlacementFn,
  runSpecificAnimations = noop
}) {
  camera = new three.PerspectiveCamera(70, window.innerWidth / window.innerHeight, 0.01, 10);
  camera.position.set(cameraPosition[0], cameraPosition[1], cameraPosition[2]);

  scene = new three.Scene();

  scene.background = new three.Color(0x867979);

  renderer = new three.WebGLRenderer({ antialias: true });
  renderer.setSize(window.innerWidth, window.innerHeight);

  controls = new three.OrbitControls(camera, renderer.domElement);
  // controls.addEventListener('change', render); // remove when using animation loop

  controls.enableZoom = true;
  controls.autoRotate = autoRotate;
  controls.autoRotateSpeed = autoRotateSpeed;

  let meshContext = {};

  buildPlacementFn(scene, meshContext);

  document.querySelector('#root').appendChild(renderer.domElement);

  let animate = createBasicAnimator(controls, meshContext, runSpecificAnimations);
  animate();
}

function createBasicAnimator(controls, meshContext, runSpecificAnimations = noop) {
  function basicAnimate() {
    requestAnimationFrame(basicAnimate);

    if (controls) {
      // required if controls.enableDamping = true, or if controls.autoRotate = true
      controls.update();
    }

    runSpecificAnimations(meshContext);

    render();
  }

  return basicAnimate;
}

function render() {
  renderer.render(scene, camera);
}



