export default function initControls(props) {
  window.addEventListener('keydown', e => {
    // ctrl+anykey will ignored
    if (e.ctrlKey) return;

    Object.keys(props).forEach(keyCode => {
      if (keyCode === e.code) {
        props[keyCode]();
      }
    });
  });
}



