export function getRandomIntBetween(min, max) {
  return Math.floor(Math.random() * (max - min)) + min;
}

export function getRandomBetween(min, max) {
  return Math.random() * (max - min) + min;
}

export function evalIntAvg(a, b) {
  return Math.floor((a + b) / 2);
}

export function toModuleNumber(number) {
  return number > 0 ? number : -1 * number;
}

function changeCoords(coords, deltas) {
  return deepClone([
    coords[0] + deltas[0],
    coords[1] + deltas[1],
    coords[2] + deltas[2]
  ]);
}

export function getAjacentPositions(position, minIndex, maxIndex) {
  let deltas = [
    [+1, 0, 0], [0, +1, 0], [0, 0, +1],
    [-1, 0, 0], [0, -1, 0], [0, 0, -1]
  ];

  let result = [];
  deltas.forEach(delta => {
    let newPosition = changeCoords(position, delta);
    if (isPositionValid(newPosition, minIndex, maxIndex)) {
      result.push(newPosition);
    }
  });

  return result;
}

function isPositionValid(position, minIndex, maxIndex) {
  let x = position[0];
  let y = position[1];
  let z = position[2];

  if (x < minIndex || x > maxIndex) return false;
  if (y < minIndex || y > maxIndex) return false;
  if (z < minIndex || z > maxIndex) return false;

  return true;
}

export function getAjacentEmptyPlaces(position, fillMatrix) {
  let allAjacentPositions = getAjacentPositions(
    position,
    fillMatrix.minimalIndex,
    fillMatrix.maximalIndex
  );

  // TODO 1: make functional way
  let result = [];
  allAjacentPositions.forEach(ajacentPosition => {
    let x = ajacentPosition[0];
    let y = ajacentPosition[1];
    let z = ajacentPosition[2];
    if (!fillMatrix[x][y][z]) {
      result.push(ajacentPosition);
    }
  });

  return result;
}

export function getRandomFromArray(items) {
  return items[Math.floor(Math.random() * items.length)];
}

export function getRandomAxis() {
  return getRandomFromArray(['x', 'y', 'z']);
}

export function round100(number) {
  return Number((number).toFixed(2));
}

export const noop = () => {};

export const deepClone = obj => {
  if (!obj) {
    throw new Error('Can not clone empty link');
  }

  return JSON.parse(JSON.stringify(obj));
}

export function evalMinimals(fromCoord, toCoord) {
  let minX = toCoord[0] >= fromCoord[0] ? fromCoord[0] : toCoord[0];
  let minY = toCoord[1] >= fromCoord[1] ? fromCoord[1] : toCoord[1];
  let minZ = toCoord[2] >= fromCoord[2] ? fromCoord[2] : toCoord[2];
  return {minX, minY, minZ};
}

export function evalDeltas(fromCoord, toCoord) {
  return {
    deltaX: toModuleNumber(toCoord[0]) - toModuleNumber(fromCoord[0]),
    deltaY: toModuleNumber(toCoord[1]) - toModuleNumber(fromCoord[1]),
    deltaZ: toModuleNumber(toCoord[2]) - toModuleNumber(fromCoord[2])
  };
}



