import React from 'react';
import ReactDOM from 'react-dom';
import Debuggers from './comp/Debuggers';

export default function initSlidersDebugger() {
  let debuggers = window.__debuggers;
  let reRender = () => render(debuggers);
  reRender();
}

function render(debuggers) {
  ReactDOM.render(
    <Debuggers debuggers={debuggers} />,
    document.getElementById('slider-debuggers')
  );
}



